body.theme-color--pikachu_yellow {
/* -------------------------------------- */
/*                PIKACHU                 */
/*       theme-color--pikachu_yellow      */
/* -------------------------------------- */
/* THEME COLOR */
/* couleur du thème déclinée dans plusieurs
teintes et nuances */
--theme--light: #fefde5;
--theme--vivid: #ffca45;
--theme--dull: #f4e664;

/* couleur sombre qui se marie avec la couleur
principale du thème sans l'éclipser */
--theme--dark: #383b46;
--theme--black: #0e0f0f;

/* ACCENT COLOR */
/* couleur liée au thème qui met les choses en
valeur ou en contraste */ 
--accent--dark: #d3a529;
--accent--medium: #f2c44c;
--accent--light: #f9e0a2;

/* TAINTED COLORS */
/* couleur liée au thème marquant une
transition avec une couleur neutre */
--theme--tainted-grey: #fcf9e5;
--accent--tainted-grey: #fde6b0;
}